import React, { Component } from "react";
import PropTypes from "prop-types";
import { PENDING, ACTIVE, BCURDecoder } from "unchained-wallets";
import QrReader from "react-qr-reader";
import {
  Grid,
  Button,
  Box,
  FormHelperText,
  LinearProgress,
} from "@material-ui/core";
import Copyable from "../Copyable";

const QR_CODE_READER_DELAY = 100; // ms?

class HermitReader extends Component {
  constructor(props) {
    super(props);
    this.state = {
      totalParts: 0,
      partsReceived: 0,
      status: PENDING,
      error: "",
    };
    this.decoder = new BCURDecoder();
  }

  render = () => {
    const { status, error, partsReceived, totalParts } = this.state;
    const { interaction, width, startText } = this.props;

    if (status === PENDING) {
      const commandMessage = interaction.messageFor({
        state: status,
        code: "hermit.command",
      });
      return (
        <div>
          <p>{commandMessage.instructions}</p>
          <Grid container justify="center" className="mb-2">
            <Copyable text={commandMessage.command} showText={false}>
              <code>
                <strong>{commandMessage.mode}&gt;</strong>{" "}
                {commandMessage.command}
              </code>
            </Copyable>
          </Grid>
          <p>When you are ready, scan the QR code produced by Hermit:</p>
          <Box mt={2}>
            <Button
              variant="contained"
              color="primary"
              className="mt-2"
              size="large"
              onClick={this.handleStart}
            >
              {startText}
            </Button>
          </Box>
        </div>
      );
    }

    if (status === ACTIVE) {
      const progress = totalParts > 0 ? (partsReceived / totalParts) * 100 : 0;
      return (
        <Grid container direction="column">
          <Grid item>
            <QrReader
              delay={QR_CODE_READER_DELAY}
              onError={this.handleError}
              onScan={this.handleScan}
              style={{ width }}
              facingMode="user"
              qrFeedback
            />
          </Grid>
          <Grid item>
            {progress === 0 ? (
              <div>
                <LinearProgress />
                <p>Waiting for first QR code...</p>
              </div>
            ) : (
              <div>
                <LinearProgress variant="determinate" value={progress} />
                <p>
                  Scanned {partsReceived} of {totalParts} QR codes...
                </p>
              </div>
            )}
          </Grid>
          <Grid item>
            <Button
              variant="contained"
              color="secondary"
              size="small"
              onClick={this.handleStop}
            >
              Cancel
            </Button>
          </Grid>
        </Grid>
      );
    }

    if (status === "error" || status === "success") {
      return (
        <div>
          <FormHelperText error>{error}</FormHelperText>
          <Button
            variant="contained"
            color="secondary"
            size="small"
            onClick={this.handleStop}
          >
            Reset
          </Button>
        </div>
      );
    }

    return null;
  };

  handleStart = () => {
    const { onStart } = this.props;
    this.setState({ status: ACTIVE, error: "" });
    if (onStart) {
      onStart();
    }
  };

  handleError = (error) => {
    const { onClear } = this.props;
    this.setState({ status: "error", error: error.message });
    if (onClear) {
      onClear();
    }
  };

  handleScan = (qrCodeString) => {
    const { onSuccess, interaction } = this.props;
    if (qrCodeString) {
      if (!this.decoder.isComplete()) {
        this.decoder.receivePart(qrCodeString);
      }
      const { totalParts, partsReceived } = this.decoder.progress();
      this.setState({ totalParts, partsReceived });
      if (this.decoder.isComplete()) {
        if (this.decoder.isSuccess()) {
          const data = this.decoder.data();
          onSuccess(interaction.parse(data));
          this.setState({ status: "success" });
        } else {
          const message = this.decoder.errorMessage();
          this.handleError({ message });
        }
      }
    }
  };

  handleStop = () => {
    const { onClear } = this.props;
    this.setState({
      status: PENDING,
      error: "",
      totalParts: 0,
      partsReceived: 0,
    });
    this.decoder.reset();
    if (onClear) {
      onClear();
    }
  };
}

HermitReader.propTypes = {
  onStart: PropTypes.func.isRequired,
  onSuccess: PropTypes.func.isRequired,
  onClear: PropTypes.func.isRequired,
  width: PropTypes.string,
  startText: PropTypes.string,
  interaction: PropTypes.shape({
    messageFor: PropTypes.func,
    parse: PropTypes.func,
  }).isRequired,
};

HermitReader.defaultProps = {
  width: "256px",
  startText: "Scan",
};

export default HermitReader;
