import React from "react";
import PropTypes from "prop-types";

import { Grid, Button } from "@material-ui/core";
import QRCode from "qrcode.react";

class HermitDisplayer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      paused: false,
      timer: null,
      index: 0,
    };
  }

  componentDidMount = () => {
    this.startTimer();
  };

  componentWillUnmount = () => {
    this.cancelTimer();
  };

  transitionToNextQRCode = () => {
    const { parts } = this.props;
    const { index, paused } = this.state;
    const { rate } = this.props;
    const nextIndex = index === parts.length - 1 ? 0 : index + 1;
    if (paused) {
      this.setState({ index: nextIndex });
    } else {
      const timer = setTimeout(this.transitionToNextQRCode, rate);
      this.setState({ timer, index: nextIndex });
    }
  };

  cancelTimer = () => {
    const { timer } = this.state;
    if (timer) {
      clearTimeout(timer);
      this.setState({ paused: true, timer: null });
    }
  };

  startTimer = () => {
    const { rate } = this.props;
    this.cancelTimer();
    const timer = setTimeout(this.transitionToNextQRCode, rate);
    this.setState({ paused: false, timer });
  };

  togglePlay = (event) => {
    event.preventDefault();
    const { paused } = this.state;
    if (paused) {
      this.startTimer();
    } else {
      this.cancelTimer();
    }
  };

  render = () => {
    const { width, nextText, onCancel, onNext, parts } = this.props;
    const { index } = this.state;
    const part = parts[index];
    return (
      <div style={{ padding: "50px" }}>
        <h6>
          QR Code {index + 1} of {parts.length}
        </h6>
        <button type="button" onClick={this.togglePlay}>
          <QRCode size={width} value={part} level="L" />
        </button>

        <p>Scan the QR codes above into Hermit.</p>
        {onNext && (
          <p>
            When you are done, hit the &ldquo;{nextText}&rdquo; button to
            continue.
          </p>
        )}
        <Grid container spacing={10}>
          {onCancel && (
            <Grid item xs>
              <Button onClick={onCancel}>Cancel</Button>
            </Grid>
          )}
          {onNext && (
            <Grid item xs>
              <Button onClick={onNext}>{nextText}</Button>
            </Grid>
          )}
        </Grid>
      </div>
    );
  };
}

HermitDisplayer.propTypes = {
  width: PropTypes.number,
  rate: PropTypes.number,
  parts: PropTypes.arrayOf(PropTypes.string),
  onCancel: PropTypes.func,
  onNext: PropTypes.func,
  nextText: PropTypes.string,
};

HermitDisplayer.defaultProps = {
  width: 640,
  rate: 200, // ms per QR code displayed
  nextText: "Scan",
  onCancel: null,
  onNext: null,
  parts: [],
};

export default HermitDisplayer;
